// src/store/modules/cart.js

export default {
  namespaced: true,
  state: {
    items: [],
  },
  getters: {
    cartTotal: (state) =>
      state.items.reduce((sum, item) => sum + item.price * (item.quantity || 1), 0),
    cartCount: (state) => 
      state.items.reduce((sum, item) => sum + (item.quantity || 1), 0),
    cartItems: (state) => state.items,
  },
  mutations: {
    ADD_ITEM(state, product) {
      const existing = state.items.find(item => item.id === product.id)
      if (existing) {
        existing.quantity = (existing.quantity || 1) + 1
      } else {
        state.items.push({ ...product, quantity: 1 })
      }
    },
    UPDATE_QUANTITY(state, { index, quantity }) {
      if (quantity > 0) {
        state.items[index].quantity = quantity
      }
    },
    REMOVE_ITEM(state, index) {
      state.items.splice(index, 1)
    },
    CLEAR_CART(state) {
      state.items = []
    },
  },
  actions: {
    addItem({ commit }, product) {
      commit('ADD_ITEM', product)
    },
    updateQuantity({ commit }, { index, quantity }) {
      commit('UPDATE_QUANTITY', { index, quantity })
    },
    removeItem({ commit }, index) {
      commit('REMOVE_ITEM', index)
    },
    clearCart({ commit }) {
      commit('CLEAR_CART')
    },
  },
}
