<template>
  <v-dialog
    :model-value="show"
    max-width="600"
    @update:model-value="$emit('close')"
    role="dialog"
    aria-label="Carrito de compras"
    aria-modal="true"
  >
    <v-card rounded="lg">
      <!-- Header -->
      <v-card-title class="d-flex justify-space-between align-center pa-6 bg-light">
        <span class="text-h6 font-weight-bold">
          ðŸ›’ Mi Carrito
        </span>
        <v-btn
          icon
          variant="text"
          size="small"
          @click="$emit('close')"
          aria-label="Cerrar carrito"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>

      <v-divider />

      <!-- Contenido del carrito -->
      <v-card-text class="pa-6">
        <div v-if="items.length === 0" class="text-center py-12">
          <v-icon size="64" color="medium-emphasis" class="mb-4">
            mdi-shopping-cart-outline
          </v-icon>
          <p class="text-body-2 text-medium-emphasis mt-4">
            Tu carrito estÃ¡ vacÃ­o
          </p>
          <p class="text-caption text-medium-emphasis">
            Agrega productos para comenzar a comprar
          </p>
        </div>

        <div v-else>
          <!-- Items -->
          <v-list class="pa-0 mb-4" role="list" aria-label="ArtÃ­culos en el carrito">
            <v-list-item
              v-for="(item, index) in items"
              :key="index"
              class="mb-3 pa-3 rounded-lg cart-item"
              role="listitem"
            >
              <template #prepend>
                <v-img
                  :src="item.image"
                  height="70"
                  width="70"
                  class="rounded me-3"
                  :alt="`Imagen de ${item.name}`"
                />
              </template>

              <div class="flex-grow-1">
                <v-list-item-title class="font-weight-semibold mb-2">
                  {{ item.name }}
                </v-list-item-title>
                <v-list-item-subtitle class="text-caption mb-2">
                  {{ item.category }}
                </v-list-item-subtitle>

                <!-- Quantity Controls -->
                <div class="d-flex align-center gap-2">
                  <v-btn
                    size="x-small"
                    variant="outlined"
                    icon
                    @click="decreaseQuantity(index)"
                    :disabled="item.quantity <= 1"
                  >
                    <v-icon size="small">mdi-minus</v-icon>
                  </v-btn>
                  <span class="text-body-2 font-weight-bold px-2">
                    {{ item.quantity || 1 }}
                  </span>
                  <v-btn
                    size="x-small"
                    variant="outlined"
                    icon
                    @click="increaseQuantity(index)"
                  >
                    <v-icon size="small">mdi-plus</v-icon>
                  </v-btn>
                </div>
              </div>

              <template #append>
                <div class="text-right">
                  <div class="text-body-2 font-weight-bold text-primary mb-2">
                    ${{ (item.price * (item.quantity || 1)).toFixed(2) }}
                  </div>
                  <v-btn
                    size="x-small"
                    color="error"
                    variant="text"
                    icon
                    @click.stop="removeItem(index)"
                    :aria-label="`Eliminar ${item.name} del carrito`"
                  >
                    <v-icon>mdi-trash-outline</v-icon>
                  </v-btn>
                </div>
              </template>
            </v-list-item>
          </v-list>

          <!-- Resumen -->
          <v-divider class="my-4" />

          <div class="summary-section" role="region" aria-label="Resumen de la compra">
            <div class="d-flex justify-space-between mb-3">
              <span class="text-body-2">Subtotal:</span>
              <span class="text-body-2 font-weight-bold" aria-label="`Subtotal: ${subtotal} pesos`">${{ subtotal }}</span>
            </div>
            <div class="d-flex justify-space-between mb-3">
              <span class="text-body-2">Impuesto (10%):</span>
              <span class="text-body-2 font-weight-bold" aria-label="`Impuesto: ${tax} pesos`">${{ tax }}</span>
            </div>
            <v-divider class="my-3" />
            <div class="d-flex justify-space-between">
              <span class="text-h6 font-weight-bold">Total:</span>
              <span class="text-h6 font-weight-bold text-primary" aria-label="`Total a pagar: ${total} pesos`">${{ total }}</span>
            </div>
          </div>
        </div>
      </v-card-text>

      <!-- Acciones -->
      <v-card-actions v-if="items.length > 0" class="pa-6 d-flex gap-3 actions-cart-dialog">
        <v-btn
          variant="outlined"
          color="primary"
          size="large"
          class="btn-continue"
          @click="$emit('close')"
          aria-label="Continuar comprando"
        >
          Continuar comprando
        </v-btn>
        <v-btn
          color="success"
          variant="flat"
          prepend-icon="mdi-whatsapp"
          class="flex-grow-1 btn-send-order"
          size="large"
          @click="sendMessage"
          aria-label="Enviar pedido por WhatsApp"
        >
          Enviar Pedido
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "CartDialog",
  props: {
    show: Boolean,
    store: Object,
  },
  computed: {
    ...mapState("cart", ["items"]),
    subtotal() {
      return this.items.reduce((sum, item) => sum + item.price * (item.quantity || 1), 0).toFixed(2);
    },
    tax() {
      return (this.subtotal * 0.1).toFixed(2);
    },
    total() {
      return (parseFloat(this.subtotal) + parseFloat(this.tax)).toFixed(2);
    },
  },
  methods: {
    increaseQuantity(index) {
      this.$store.commit("cart/UPDATE_QUANTITY", {
        index,
        quantity: (this.items[index].quantity || 1) + 1,
      });
    },
    decreaseQuantity(index) {
      const currentQty = this.items[index].quantity || 1;
      if (currentQty > 1) {
        this.$store.commit("cart/UPDATE_QUANTITY", {
          index,
          quantity: currentQty - 1,
        });
      }
    },
    removeItem(index) {
      this.$store.commit("cart/REMOVE_ITEM", index);
    },
    sendMessage() {
      if (!this.store) return;

      const phone = this.store.whatsappPhone;
      const storeName = this.store.name;
      const addressBlock = this.getAddressBlock();

      // Crear lista de items con cantidades
      const itemsList = this.items
        .map(
          (item) =>
            `ðŸ“¦ ${item.name}\n   Cantidad: ${item.quantity || 1} Ã— $${item.price} = $${(
              item.price * (item.quantity || 1)
            ).toFixed(2)}`
        )
        .join("\n\n");

      const message = encodeURIComponent(
        `*PEDIDO DESDE ${storeName.toUpperCase()}* ðŸ›ï¸\n\n` +
          `${itemsList}\n\n` +
          `${"â”€".repeat(40)}\n` +
          `ðŸ’° *RESUMEN DEL PEDIDO*\n` +
          `Subtotal: $${this.subtotal}\n` +
          `Impuestos (10%): $${this.tax}\n` +
          `*TOTAL: $${this.total}*\n` +
          `${"â”€".repeat(40)}\n\n` +
          `${addressBlock}` +
          `Por favor, confirma:\n` +
          `âœ“ Disponibilidad de productos\n` +
          `âœ“ Formas de pago\n` +
          `âœ“ Tiempo de entrega\n\n` +
          `Â¡Gracias por tu compra! ðŸŽ‰`
      );

      window.open(`https://wa.me/${phone}?text=${message}`, "_blank");
      this.$emit("send-message");
      this.$emit("close");
    },
    getAddressBlock() {
      const addressInput = window.prompt(
        "Comparte tu direcciÃ³n de entrega (opcional) o pega tu ubicaciÃ³n.",
        ""
      );
      const address = (addressInput || "").trim();

      if (address) {
        return `ðŸ“ DirecciÃ³n de entrega:\n${address}\n\n`;
      }

      return "ðŸ“ Comparte tu direcciÃ³n o envÃ­a tu ubicaciÃ³n para coordinar la entrega.\n\n";
    },
  },
};
</script>

